import React, { useState , useEffect } from "react";

import { Grid, Card, Switch , Modal } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

//Components
import Layout from "./Components/Layout/Layout";
import User from "./Components/UserDetails/User";
import UserSignIn from "./Components/User/SignIn";
import UserSignUp from "./Components/User/SignUp";
import Post from './Components/SendPost/Post'
import Lists from "./Components/Lists/Lists";
import Comment from './Components/Comment/Comment'

//UTILS
import axios from 'axios'

const useStyles = makeStyles((theme) => ({
  main: {
    height: "100vh",
    marginTop: theme.spacing(1),
  },
  grid: {
    border: `1px solid black`,
  },
  cardUser: {
    margin: theme.spacing(1),
  },
  cardTweet: {
    margin: theme.spacing(1),
    height: "100vh",
    overflow: "auto",
  },
  switch: {
    float: "right",
  },
}));
function App() {
  const clasess = useStyles();
  

  const [posts, setposts] = useState([])
  
  const [changeButton, setChangeButton] = useState(false);

  const [isLogged, setIsLogged] = useState(false);
  
  const [userInfo, setuserInfo] = useState({})

  const [openModal, setopenModal] = useState(false)

  const [commentData, setcommentData] = useState()

  const [postData, setpostData] = useState('')

  const [commentPostData, setcommentPostData] = useState('')


  //-------

  //SIGN UP
  
  const [form, setForm] = useState({
    email: "",
    password: "",
  });
  const updateField = (e) => {
    setForm({
      ...form,
      [e.target.name]: e.target.value,
    });
  };
  const submitButton = async () => {
    console.log(form);
    if(form.email && form.password){
      let data = JSON.stringify({
        email: form.email,
        password: form.password,
    })
      await axios({
        method: 'post',
        url: 'http://localhost:8000/signUp',
        headers: {'Content-Type':'application/json'},
        data
      })
      .then(res=>{
        setuserInfo({
          userName:res.data.userName ,
          mail:res.data.mail,
          photoUrl:res.data.photoURL
          });
          setIsLogged(true);
      })
      .catch(err=>console.log(err.message))
    }
  };
  
  //-------

  //SIGN IN

  const [signIn, setSignIn] = useState({
    email: "",
    password: "",
  });
  const formFeild = (e) => {
    setSignIn({
      ...signIn,
      [e.target.name]: e.target.value,
    });
  };
  const submitSign = async () => {
    if(signIn.email && signIn.password){
      let data = JSON.stringify({
        email: signIn.email,
        password: signIn.password,
    })
      await axios({
        method: 'post',
        url: 'http://localhost:8000/signIn',
        headers: {'Content-Type':'application/json'},
        data
      })
      .then(res=>{
        setuserInfo({
          userName:res.data.user.displayName ,
          mail:res.data.user.email,
          photoUrl:res.data.user.photoURL
          });
          setIsLogged(true);
      })
      .catch(err=>console.log(err.message))
    }

  };

  //-------

    //FETCH DATA FROM API
    useEffect(async() => {
      await axios({
        method: 'get',
        url: 'http://localhost:8000/allPosts'
      })
      .then(res=>{
        setposts(res.data)
        
      })
      .catch(err=>console.log(err.message))
    }, [])


  //LOGOUT
   const submitLogOut = async () =>{
    await axios({
      method: 'post',
      url: 'http://localhost:8000/logOut',
    })
    .then(res => {
      console.log(res);
      setIsLogged(false);
    })
    .catch(err=>console.log(err.message))
   }
   //COMMENT SHOW
  const submitComment =  async (id) =>{
    console.log(id)
    await axios({
      method: 'get',
      url: `http://localhost:8000/getOnePost/${id}`,
    })
    .then(res=>{
      console.log(res.data)
      setcommentData(res.data)
      setopenModal(true);
    })
    .catch(err=>console.log(err.message))
  }
  const handleClose = () => {
    setopenModal(false);

  }
  //ADD POST
  const addPost = async () =>{
    await axios({
      method: 'post',
      url: 'http://localhost:8000/sendPost',
      headers: {'Content-Type':'application/json'},
      data:{
        message:postData
      }
    })
    .then(res=>{
      console.log(res);
      
    })
    .catch(err=>console.log(err.message))

  }

  //COMMENT POST

  const commentPost = async (x) => {
    await axios({
      method: 'post',
      url: `http://localhost:8000/commentPost/${x}`,
      headers: {'Content-Type':'application/json'},
      data:{
        message:commentPostData
      }
    })
    .then(res=>{
      console.log(res);
      setopenModal(false)
    })
    .catch(err=>console.log(err.message))
  }

  return (
    <Layout>
      <Grid container className={clasess.main}>
        <Grid item xs="12" md="3">
          {isLogged ? <User submitLogOut={submitLogOut} photoUrl={userInfo.photoUrl} userName={userInfo.userName} mail={userInfo.mail} /> : null}
        </Grid>
        <Grid item xs="12" md="6">
                  <Modal
            open={openModal}
            onClose={handleClose}
            aria-labelledby="simple-modal-title"
            aria-describedby="simple-modal-description"
          >
            <Comment commentPost={commentPost} commentPostData={commentPostData} setcommentPostData={setcommentPostData} handleClose={handleClose}  isLogged={isLogged} commentData={commentData}/>
          </Modal>
          <Card className={clasess.cardTweet}>
            {isLogged ? <Post postData={postData} setpostData={setpostData} addPost={addPost}/> :null}
            {posts.map(post=>(
              <Lists submitComment={(e)=>submitComment(e)} isLogged={isLogged} postId={post.postId}  userName={post.userName} message={post.message} photoUrl={post.photoUrl} comments={post.comments} createdAt={post.createdAt} like={post.like} />
            ))}
          </Card>
        </Grid>
        <Grid item xs="12" md="3">
          {isLogged ? null : (
            <>
              <Switch
                checked={changeButton}
                onChange={() => setChangeButton(!changeButton)}
                name="checkedA"
                className={clasess.switch}
                inputProps={{ "aria-label": "secondary checkbox" }}
              />
              {changeButton ? (
                <UserSignUp
                  form={form}
                  updateField={updateField}
                  submitButton={submitButton}
                  setForm={setForm}
                />
              ) : (
                <UserSignIn
                signIn={signIn}
                formFeild={formFeild}
                submitSign={submitSign}
                setSignIn={setSignIn}
                />
              )}
            </>
          )}
        </Grid>
      </Grid>
    </Layout>
  );
}

export default App;
